
import { Dialog } from '@material-ui/core';
import React, { useEffect, useState } from 'react';

export interface LazyDialogProps {
  children: React.ReactNode;
  isOpen: boolean;
}

const dialogCloseAnimationDuration = 500;

function LazyDialog({ isOpen, children }: LazyDialogProps) {
  const [isOpenState, setOpenState] = useState(isOpen);
  // Defer close of the dialog for the animation to complete
  useEffect(
    () => {
      let timeoutId: NodeJS.Timeout;
      if (isOpenState !== isOpen) {
        if (!isOpen) {
          timeoutId = setTimeout(() => {
            setOpenState(isOpen);
          }, dialogCloseAnimationDuration);
        } else {
          setOpenState(isOpen);
        }
      }
      return () => clearTimeout(timeoutId);
    },
    // eslint-disable-next-line react-hooks/exhaustive-deps
    [isOpen],
  );

  return (
    <React.Suspense fallback={<Dialog open />}>
      {isOpenState && children}
    </React.Suspense>
  )
}

export default LazyDialog;
